import React, {useState, useEffect} from 'react';
import {Button} from 'reactstrap';

import ComicDisplay from './ComicDisplay';

const ComicResults = (props) => {
    const [comicResults, setComicResults] = useState([]);
    const [offset, setOffset] = useState(0);

    const baseURL = 'https://gateway.marvel.com:443/v1/public/comics?';
    const apikey = '94bfc2c1e9454783329b955ba1751491'
    
    const increaseOffset = () => {
        setOffset(20);
        console.log(offset);
    }

    const fetchComicResults = () => {
        let url = `${baseURL}titleStartsWith=${props.comicName}&offset=${offset}&apikey=${apikey}`;
        console.log(url);
        fetch(url)
        .then(result => result.json())
        .then(json => {setComicResults(json.data.results)})
    }

    useEffect(() => {
        fetchComicResults();
    }, [])

    return(
        <div>
            <div>
            {comicResults.length == 0 ? <></> : <ComicDisplay comicResults={comicResults} searchBar={props.searchBar} searchBarOn={props.searchBarOn} searchBarOff={props.searchBarOff} detailView={props.detailView} setDetailView={props.setDetailView} />}
            </div>
            <div className="navButtons">
            {comicResults.length ==20 ? <Button className="nextPage" onClick={()=>{increaseOffset()}}>Next Page</Button> : null}
            </div>
        </div>
    )
}

export default ComicResults;