import React, {useState} from 'react';
import {Card, CardImg, CardBody, CardTitle, CardSubtitle, Button} from 'reactstrap';

import DetailView from './DetailView'

const ComicDisplay = (props) => {
    // const [detailView, setDetailView] = useState(false);
    const [comicToView, setComicToView] = useState({});

    const detailViewOn = (comic) => {
        props.setDetailView(true);
        setComicToView(comic);
    }

    const detailViewOff = () => {
        props.setDetailView(false);
    }

    const comicDisplayGrid = () => {
        console.log(props.comicResults);
        return props.comicResults.map((comic, ind) => {
            const p = comic.thumbnail.path;
            const e = comic.thumbnail.extension;

            return (
                  <Card key={ind} className="card">
                    <Button className="button" onClick={()=> {detailViewOn(comic); props.searchBarOff()}}><CardImg top width="100%" src={`${p}/standard_xlarge.${e}`} alt="Image Not Available" /></Button>
                    <CardBody>
                      <CardTitle>{comic.title}</CardTitle>
                    </CardBody>
                  </Card>
              );
        })
    }
    
    return(
        <div>
        {
            props.detailView ? 
            <DetailView comicToView={comicToView} detailViewOff={detailViewOff} searchBarOn={props.searchBarOn} /> : 
        <div>
             <Button onClick={()=>{props.searchBarOn()}}>Back To Search</Button>
             <p>Click An Image For Detail View</p>
                <div className="container">
                {comicDisplayGrid()}
                </div>
        </div>
    }
        </div>
    )
}

export default ComicDisplay;